//-------------------------------------------------------------------
//--- collectRefs()
//-------------------------------------------------------------------
export function collectRefs(root = document) {
    const refs = {};
    root.querySelectorAll("[data-ref]").forEach(el => {
        const name = el.dataset.ref.trim();
        if (name) refs[name] = el;
    });
    return refs;
}

//-------------------------------------------------------------------
//--- escapeHtml()
//-------------------------------------------------------------------
export function escapeHtml(s = '') {
    return String(s)
    .replace(/&/g, '&amp;')
    .replace(/</g, '&lt;')
    .replace(/>/g, '&gt;')
    .replace(/"/g, '&quot;')
    .replace(/'/g, '&#39;');
}