import {Addon_Chrome} from './addon_chrome/index.js'
import {Addon_Docs} from './addon_docs/index.js'
import {Platform} from './core/utils/utils.js';

document.addEventListener('DOMContentLoaded', () => {

    if (Platform.isChrome) {
        Addon_Chrome.init();
    } else if (Platform.isDocs) {
        Addon_Docs.init();
    } else {
        console.warn("Keine unterstützte Plattform erkannt");
    }

    //Addon_Chrome.init(); //nur zum test auf localhost
    Addon_Docs.init(); //nur zum test auf localhost
});