export default class PH_FormText {

    //-------------------------------------------------------------------
    //--- constructor()
    //-------------------------------------------------------------------
    constructor() {
        this.type = 'form.text';
        this.desc = 'Erstellt ein einzeiliges Eingabefeld.';
        this.attrs = {};
    }

    //-------------------------------------------------------------------
    //--- chip()
    //-------------------------------------------------------------------
    chip(ph, context = {}) {
        return `<span class="chip">${ph.type}</span>`;
    }

    //-------------------------------------------------------------------
    //--- edit()
    //-------------------------------------------------------------------
    edit(ph, context = {}) {
        return `
            <div class="form-control">
                <div class="form-label">Name (optional)</div>
                <wpx-input></wpx-input>
                <div class="form-hint">Der Name des Textfelds.</div>
            </div>

            <div class="form-control">
                <div class="form-label">Standardwert (optional)</div>
                <wpx-input></wpx-input>
                <div class="form-hint">Der Standardwert des Textfelds.</div>
            </div>
        `;
    }

    //-------------------------------------------------------------------
    //--- renderUI()
    //-------------------------------------------------------------------
    renderUI(ph, context = {}) {
        const name = ph.attrs.name || '';
        return `<wpx-input label="${name}"></wpx-input>`;
    }

    //-------------------------------------------------------------------
    //--- execute()
    //-------------------------------------------------------------------
    execute(ph, context = {}) {
        return 'FORM.TEXT';
    }
}