import {TRANSLATIONS} from '../../languages/languages.js';

export class Lang {

    //-------------------------------------------------------------------
    //--- constructor()
    //-------------------------------------------------------------------
    constructor(lang = 'de') {
        this.lang = lang;
    }

    //-------------------------------------------------------------------
    //--- setLang()
    //-------------------------------------------------------------------
    setLang(lang) {
        this.lang = lang;
        document.dispatchEvent(new CustomEvent('lang-change', {detail: lang}));
    }

    //-------------------------------------------------------------------
    //--- updateUI()
    //-------------------------------------------------------------------
    updateUI() {
        document.querySelectorAll('[data-txt]').forEach(el => {
            el.innerHTML = this.t(el.dataset.txt);
        });
    }

    //-------------------------------------------------------------------
    //--- t()
    //-------------------------------------------------------------------
    t(key, params = {}) {
        const translations = TRANSLATIONS[this.lang] || TRANSLATIONS['en'];
        let text = translations[key] || key;

        for (const p in params) {
            text = text.replace(`{{${p}}}`, params[p]);
        }
        return text;
    }
}

export const lang = new Lang(navigator.language.startsWith('de') ? 'de' : 'en');
