import {formatDate} from '../../utils/files/utils.date.js';

export default class PH_Date {

    //-------------------------------------------------------------------
    //--- constructor()
    //-------------------------------------------------------------------
    constructor() {
        this.type = 'date';
        this.desc = 'Fügt das aktuelle Datum ein.';
        this.attrs = {};
    }

    //-------------------------------------------------------------------
    //--- chip()
    //-------------------------------------------------------------------
    chip(ph, context = {}) {
        const f = ph.attrs.format || "DD.MM.YYYY";
        return `<span class="chip">${ph.type}: <b>${f}</b></span>`;
    }

    //-------------------------------------------------------------------
    //--- edit()
    //-------------------------------------------------------------------
    edit(ph, context = {}) {
        const f = ph.attrs.format || "DD.MM.YYYY";
        return `
            <div class="form-control">
                <div class="form-label">Format</div>
                <wpx-input value="${f}"></wpx-input>
                <div class="form-hint">Beispiel:</div>
            </div>
        `;
    }

    //-------------------------------------------------------------------
    //--- execute()
    //-------------------------------------------------------------------
    execute(ph, context = {}) {
        const f = ph.attrs.format || "DD.MM.YYYY";
        return formatDate(new Date(), f);
    }
}