import {placeholders} from '../../../core/placeholders/placeholders.js';
import {collectRefs}  from '../../../core/utils/files/utils.html.js';

export const DrawerPlaceholder = {

    //-------------------------------------------------------------------
    //--- init()
    //-------------------------------------------------------------------
    init() {
        this.drawer = document.querySelector('#drawer-placeholder');
        this.drawer.addEventListener('click', this.handleClick.bind(this));
        this.refs = collectRefs(this.drawer);
    },

    //-------------------------------------------------------------------
    //--- show()
    //-------------------------------------------------------------------
    show(ph) {
        const phList = this.refs['placeholder-list'];
        const phDesc = this.refs['placeholder-desc'];
        const phForm = this.refs['placeholder-form'];

        const items = placeholders.list().map(p => ({
            label: p.type,
            value: p.type
        }));

        phList.setItems(items);
        phList.value = ph.type;
        phDesc.innerHTML = ph.desc;
        phForm.innerHTML = '';
        phForm.innerHTML = placeholders.edit(ph);

        this.drawer.label = `Platzhalter einfügen`;
        this.drawer.show();
        console.log(ph);
    },

    //-------------------------------------------------------------------
    //--- handleClick()
    //-------------------------------------------------------------------
    handleClick(e) {
        const action = e.target.closest('[data-action]')?.dataset.action;
        if (!action) return;
        this.runAction(action);
    },

    //-------------------------------------------------------------------
    //--- runAction()
    //-------------------------------------------------------------------
    runAction(action) {
        const map = {
            'insert': () => console.log('insert')
        };
        map[action]?.();
    }
}