export const DrawerManager = {

    drawers: {},

    //-------------------------------------------------------------------
    //--- init()
    //-------------------------------------------------------------------
    init() {
        document.querySelectorAll('wpx-drawer[id]').forEach(drawer => {
            this.drawers[drawer.id] = drawer;
        });
    },

    //-------------------------------------------------------------------
    //--- show()
    //-------------------------------------------------------------------
    show(id) {
        const drawer = this.drawers[id];
        if (!drawer) return;
        drawer.show();
    },
}