import {placeholders} from './placeholders.js';
import PH_Date from './types/date.js';
import PH_Field from './types/field.js';
import PH_FormText from './types/form.text.js';
import PH_Note from './types/note.js';

//-------------------------------------------------------------------
//--- registerPlaceholders()
//-------------------------------------------------------------------
export function registerPlaceholders() {
    placeholders.register('date', new PH_Date());
    placeholders.register('field', new PH_Field());
    placeholders.register('form.text', new PH_FormText());
    placeholders.register('note', new PH_Note());
}